import { lazy, useState } from "react";
import MiddleBlockContent from "../../content/MiddleBlockContent.json";
import AboutContent from "../../content/AboutContent.json";
import ProductContent from "../../content/ProductContent.json";
import ContactContent from "../../content/ContactContent.json";
import { Card, List, Carousel, Modal } from "antd";
import CountUp from "react-countup";
import VisibilitySensor from "react-visibility-sensor";
import { SvgIcon } from "../../common/SvgIcon";
import { Fade } from "react-awesome-reveal";
import "./style.css";

const Contact = lazy(() => import("../../components/ContactForm"));
const MiddleBlock = lazy(() => import("../../components/MiddleBlock"));
const Container = lazy(() => import("../../common/Container"));
const ScrollToTop = lazy(() => import("../../common/ScrollToTop"));
const ContentBlock = lazy(() => import("../../components/ContentBlock"));
const PhotoAlbum = lazy(() => import("react-photo-album"));

const Home = () => {
  const numberList = [
    {
      title: "Bags Produced",
      end: 600,
      svg: "orders.svg",
    },
    {
      title: "Orders Delivered",
      end: 100,
      svg: "delivery.svg",
    },
    {
      title: "Customers",
      end: 10,
      svg: "customer.svg",
    },
  ];

  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const photos = [
    { src: "img/image/1-min.JPG", width: 800, height: 600 },
    { src: "img/image/11-min.JPG", width: 1600, height: 900 },
    { src: "img/image/14-min.JPG", width: 1600, height: 900 },
    { src: "img/image/15-min.JPG", width: 1600, height: 900 },
    { src: "img/image/12-min.JPG", width: 1600, height: 900 },
    { src: "img/image/13-min.JPG", width: 1600, height: 900 },
    { src: "img/image/16-min.JPG", width: 1600, height: 900 },
    { src: "img/image/11-min.JPG", width: 1600, height: 900 },
  ];
  return (
    <div>
      <div
        style={{
          color: "#FFFFFF",
          width: "100%",
          textAlign: "center",
          position: "absolute",
          marginTop: "15%",
          zIndex: "50",
          fontWeight: 1000,
          fontSize: "6vw",
        }}
      >
        Get carried away with our bags!
      </div>
      <video
        autoPlay
        muted
        loop
        playsInline
        id="homevideo"
        data-origwidth="0"
        data-origheight="0"
        style={{ width: "100%", display: "center" }}
      >
        <source src="img/vid/vrfibc_home.mp4" type="video/mp4" />
      </video>
      {/* <div
        style={{
          position: "absolute",
          top: "0px",
          width: "100%",
          height: "50vw",
          zIndex: "-1",
        }}
      >
        <Carousel autoplay>
          <div>
            <Image
              width="100%"
              height="50vw"
              preview={false}
              src="img/image/1-min.JPG"
            ></Image>
          </div>
          <div>
            <Image
              width="100%"
              height="50vw"
              preview={false}
              src="img/image/11-min.JPG"
            ></Image>
          </div>
          <div>
            <Image
              width="100%"
              height="50vw"
              preview={false}
              src="img/image/12-min.JPG"
            ></Image>
          </div>
          <div>
            <Image
              width="100%"
              height="50vw"
              preview={false}
              src="img/image/13-min.JPG"
            ></Image>
          </div>
        </Carousel>
      </div> */}
      <Container>
        <ScrollToTop />
        <Fade delay={200}>
          <div
            style={{
              fontSize: "21px",
              fontFamily: "Motiva Sans Light",
              color: "#18216d",
              borderColor: "#ff991a",
              padding: "3vw",
              marginTop: "10px",
              borderWidth: "thick",
              borderRadius: "12px",
              fontWeight: "bold",
            }}
            className="home-intro"
          >
            We are a leading FIBC bag company offering customized bulk bags for
            various industries with a focus on quality and customer service.
            <br />
            <span style={{ fontFamily: "Motiva Sans Bold" }}>
              We know exactly what you need!
            </span>
          </div>
        </Fade>
        {/* <ContentBlock
          type="right"
          video={false}
          title={IntroContent.title}
          content={IntroContent.text}
          button={IntroContent.button}
          icon="developer.svg"
          id="intro"
        /> */}
        {/* <ContentBlock
          type="left"
          video={true}
          title={AboutContent.title}
          content={AboutContent.text}
          section={AboutContent.section}
          icon="graphs.svg"
          id="about"
        /> */}
        <div></div>
        <MiddleBlock
          title={MiddleBlockContent.title}
          content={MiddleBlockContent.text}
          button={MiddleBlockContent.button}
        />

        <PhotoAlbum layout="rows" photos={photos} padding={5} spacing={5} />
        <ContentBlock
          type="left"
          video={false}
          title={AboutContent.title}
          content={AboutContent.text}
          section={AboutContent.section}
          icon="graphs.svg"
          id="FIBC_PRODUCT"
        />
        <Fade delay={200}>
          <h6>What clients says</h6>
          <Carousel autoplay dots={true}>
            <div>
              <div style={{ display: "flex", justifyContent: "center" }}>
                <Card
                  bordered={true}
                  hoverable={false}
                  style={{
                    textAlign: "center",
                    width: "300px",
                    height: "400px",
                    fontSize: "15px",
                    fontFamily: "Motiva Sans Bold",
                    color: "#18216d",
                    borderRadius: "12px",
                    alignItems: "center",
                    borderColor: "#18216d",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  "I highly recommend VRFIBC for FIBC products. Their bags are
                  made from high-quality materials and are user-friendly. The
                  bags are durable and can withstand transportation and storage.
                  Their customer service team is also responsive and helpful
                  throughout the purchasing process".
                  <br />- Mr. Noam (Israel)
                </Card>
              </div>
            </div>
            <div>
              <div style={{ display: "flex", justifyContent: "center" }}>
                <Card
                  bordered={true}
                  hoverable={false}
                  style={{
                    textAlign: "center",
                    width: "300px",
                    height: "400px",
                    fontSize: "15px",
                    fontFamily: "Motiva Sans Bold",
                    color: "#18216d",
                    borderRadius: "12px",
                    alignItems: "center",
                    borderColor: "#18216d",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  "Highly recommended for FIBC products. Excellent quality and
                  customer service." <br />- Mr. Michel (USA)
                </Card>
              </div>
            </div>
            <div>
              <div style={{ display: "flex", justifyContent: "center" }}>
                <Card
                  bordered={true}
                  hoverable={false}
                  style={{
                    textAlign: "center",
                    width: "300px",
                    height: "400px",
                    fontSize: "15px",
                    fontFamily: "Motiva Sans Bold",
                    color: "#18216d",
                    borderRadius: "12px",
                    alignItems: "center",
                    borderColor: "#18216d",
                    display: "flex",
                    justifyContent: "center",
                  }}
                >
                  "Top-notch FIBC products and excellent customer service from
                  VRFIBC. Highly recommended" <br /> -Mr. Yasmin
                </Card>
              </div>
            </div>
          </Carousel>
        </Fade>
        <ContentBlock
          type="left"
          video={false}
          title={ProductContent.title}
          content={ProductContent.text}
          icon="waving.svg"
          id="product"
        />
        <List
          grid={{
            gutter: 16,
            xs: 1,
            sm: 1,
            md: 3,
            lg: 3,
            xl: 3,
            xxl: 3,
          }}
          dataSource={numberList}
          renderItem={(item) => (
            <List.Item>
              <Card
                title={
                  <div>
                    <SvgIcon
                      src={item.svg}
                      aria-label="homepage"
                      width="70px"
                      height="70px"
                    />
                    <div>{item.title}</div>
                  </div>
                }
                bordered={true}
                hoverable={true}
                headStyle={{
                  color: "#18216d",
                  fontWeight: "600",
                  height: "100",
                }}
                style={{
                  margin: "3%",
                  borderRadius: "5px",
                  width: "150",
                  textAlign: "center",
                  fontSize: "15px",
                  zIndex: "30",
                  backgroundColor: "white",
                  borderColor: "rgb(24 33 109 / 10%)",
                }}
              >
                <CountUp end={item.end} redraw={true} suffix="k+">
                  {({ countUpRef, start }) => (
                    <VisibilitySensor onChange={start} delayedCall>
                      <b ref={countUpRef} />
                    </VisibilitySensor>
                  )}
                </CountUp>
              </Card>
            </List.Item>
          )}
        />

        <Modal
          open={isModalOpen}
          onOk={handleOk}
          onCancel={handleCancel}
          footer={null}
          closable={true}
          style={{
            position: "absolute",
            top: "0%",
            right: "0%",
            overflow: "scroll"
          }}
        >
          <Contact
            title={ContactContent.title}
            content={ContactContent.text}
            id="contact-form"
          />
        </Modal>
        <div
          style={{ position: "fixed", bottom: "5%", right: "5%", zIndex: 200 }}
          onClick={showModal}
        >
          <SvgIcon
            src={"message.svg"}
            aria-label="homepage-message"
            width="70px"
            height="70px"
          />
        </div>
      </Container>
    </div>
  );
};

export default Home;
